footer {
    /* border: 1px solid lime; */
    border-top: 3px solid rgba(240, 248, 255, 0.644);

    /* background-color: rgba(0, 255, 255, 0.192); */
    min-height: 70px;
    width: 820px;
    position: absolute;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

footer form {
    /* border: 1px solid red; */
    margin: 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
}

footer input {
    width: 90%;
    height: fit-content;
    min-height: 40px;
}

input::target-text{
    color: rebeccapurple;
}
footer textarea {
    width: 90%;
    height: fit-content;
    min-height: 40px;
    font-size: 1.2em;
    display: flex;
    justify-content: center;
    max-height: 60px;
    border: none;
    border-radius: 10px;
    /* outline: 1px solid #6583b6; */
}

footer .submit {
    border: 1px solid rgb(167, 164, 164);
    border-radius: 7px;
    width: 70px;
    padding: 0 10px;
    font-weight: bold;
    background-color: black;
    color: whitesmoke;

}

footer .submit:hover {
    background-color: rgb(80, 79, 79);
}
